import React, { useState } from 'react';
import './ImtPage.scss';
import Header from '../../header/header';

function ImtPage() {
    const [height, setHeight] = useState('');
    const [weight, setWeight] = useState('');
    const [bmi, setBmi] = useState(null);
    const [category, setCategory] = useState('');
    const [normalWeightRange, setNormalWeightRange] = useState('');

    const calculateBmi = (e) => {
        e.preventDefault();
        const heightInMeters = height / 100;
        const bmiValue = (weight / (heightInMeters * heightInMeters)).toFixed(1);

        setBmi(bmiValue);
        let category = '';
        let normalWeightMin = (18.5 * (heightInMeters * heightInMeters)).toFixed(1);
        let normalWeightMax = (24.9 * (heightInMeters * heightInMeters)).toFixed(1);
        setNormalWeightRange(`${normalWeightMin}  - ${normalWeightMax} `);

        if (bmiValue < 16) {
            category = '   ';
        } else if (bmiValue >= 16 && bmiValue <= 18.4) {
            category = ' ()  ';
        } else if (bmiValue >= 18.5 && bmiValue <= 24.9) {
            category = '';
        } else if (bmiValue >= 25 && bmiValue <= 29.9) {
            category = '   ()';
        } else if (bmiValue >= 30 && bmiValue <= 34.9) {
            category = '  ';
        } else if (bmiValue >= 35 && bmiValue <= 39.9) {
            category = '  ';
        } else {
            category = '   ()';
        }

        setCategory(category);
    };

    return (
        <div className='wrapper_imt'>
            <div className='header-1'>
                <Header />
            </div>
            <div className="imt_wrapper">
                <div className="imt_block">
                    <h2 className='imt_tanner'>      ()</h2>
                </div>
                <div className='pictures_block'>
                    <form className='form_wrap' onSubmit={calculateBmi}>
                        <div className="form_group">
                            <label htmlFor="height"> ()</label>
                            <input
                                id="height"
                                className='form_input'
                                type="number"
                                value={height}
                                onChange={(e) => setHeight(e.target.value)}
                                required
                            />
                        </div>
                        <div className="form_group">
                            <label htmlFor="weight"> ()</label>
                            <input
                                id="weight"
                                className='form_input'
                                type="number"
                                value={weight}
                                onChange={(e) => setWeight(e.target.value)}
                                required
                            />
                        </div>
                        <button type='submit' className='button_calculate'></button>
                    </form>
                </div>
                <div className='pictures_block'>
                    <h2 className='imt_lead'></h2>
                    <p>  : {bmi}</p>
                    <p>: {category}</p>
                    <p>  : {normalWeightRange}</p>
                </div>
                <div className='pictures_block'>
                    <p className='imt_lead'>         .</p>
                </div>
                <div className='pictures_block'>
                    <h2 className='imt_lead'>  </h2>
                    <ul className='imt_content_list'>
                        <li className='imt_content_item'>
                            <span> 16</span>
                                
                        </li>
                        <li className='imt_content_item'>
                            <span>1618,4</span>
                              ()  
                        </li>
                        <li className='imt_content_item'>
                            <span>18,524,9</span>
                             
                        </li>
                        <li className='imt_content_item'>
                            <span>2529,9</span>
                                ()
                        </li>
                        <li className='imt_content_item'>
                            <span>3034,4</span>
                               
                        </li>
                        <li className='imt_content_item'>
                            <span>3539,9</span>
                               
                        </li>
                        <li className='imt_content_item'>
                            <span>40  </span>
                                ()
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default ImtPage;