import React, { useState, useRef, useEffect } from 'react';
import { Link } from 'react-router-dom'; //  Link
import PavlovPng from '../header/icon/pavlov3.png'
import './header.scss';

function Header() {
    const [showCalculators, setShowCalculators] = useState(false);
    const [showScales, setShowScales] = useState(false);
    const [showForms, setShowForms] = useState(false);
    const [showTables, setShowTables] = useState(false);

    const headerRef = useRef(null); //    

    //     
    const hideDropdowns = () => {
        setShowCalculators(false);
        setShowScales(false);
        setShowForms(false);
        setShowTables(false);
    };

    //      
    const toggleCalculators = () => {
        setShowCalculators((prev) => !prev);
        setShowScales(false); //      
        setShowForms(false); //      
        setShowTables(false); //      

    };

    //      
    const toggleScales = () => {
        setShowScales((prev) => !prev);
        setShowCalculators(false); //      
        setShowForms(false); //      
        setShowTables(false); //      
    };

    //      
    const toggleForms = () => {
        setShowForms((prev) => !prev);
        setShowCalculators(false); //      
        setShowScales(false); //      
        setShowTables(false); //      
    };

    //      
    const toggleTables = () => {
        setShowTables((prev) => !prev);
        setShowCalculators(false); //      
        setShowForms(false); //      
        setShowScales(false); //      
    };

    //    
    const handleClickOutside = (event) => {
        if (headerRef.current && !headerRef.current.contains(event.target)) {
            hideDropdowns(); //   
        }
    };

    //         
    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    return (
        <div className='header' ref={headerRef}>
            <Link className='header_href' to="/">
                <img className="img_pavlov" src={PavlovPng}></img>
                <h1 className="assistant_header"> </h1>
            </Link>
            <div className="menu_section">
                <h2 className='calculators' onClick={toggleCalculators}></h2>
                {showCalculators && (
                    <ul className="dropdown">
                        <li><Link target="_blank" to="/isa-calc"> </Link></li>
                        <li><Link target="_blank" to="/imt-calc"> </Link></li>
                        <li><Link target="_blank" to="/roma-calc">  ROMA</Link></li>
                        <li><Link target="_blank" to="/rmi-calc">  RMI I</Link></li>
                        <li><Link target="_blank" to="/intensive-calc"> </Link></li>
                    </ul>
                )}
            </div>
            <div className="menu_section">
                <h2 className='scales' onClick={toggleScales}></h2>
                {showScales && (
                    <ul className="dropdown">
                        <li><Link target="_blank" to="/gallay-scale"> -</Link></li>
                        <li><Link target="_blank" to="/hurt-scale">  </Link></li>
                        <li><Link target="_blank" to="/ludwig-scale"> </Link></li>
                        <li><Link target="_blank" to="/baden-scale">POP-Q  Baden-Walker</Link></li>
                        <li><Link target="_blank" to="/musa-scale">  , MUSA 2022</Link></li>
                        <li><Link target="_blank" to="/figo-scale"> :  FIGO</Link></li>
                        <li><Link target="_blank" to="/tanner-scale">   </Link></li>
                    </ul>
                )}
            </div>
            <div className="menu_section">
                <h2 className='scales' onClick={toggleForms}></h2>
                {showForms && (
                    <ul className="dropdown">
                        <li><Link target="_blank" to="/vteo-form">        </Link></li>
                        <li><Link target="_blank" to="/preeclampsia-form">    </Link></li>
                        <li><Link target="_blank" to="/edinburg-form">   </Link></li>
                        <li><Link target="_blank" to="/checklist-form">-     </Link></li>
                        <li><Link target="_blank" to="/oral-form">-      </Link></li>
                        <li><Link target="_blank" to="/metal-form">-    </Link></li>
                        <li><Link target="_blank" to="/lng-form">-   -</Link></li>
                        <li><Link target="_blank" to="/implant-form">-      </Link></li>
                        <li><Link target="_blank" to="/injection-form">-     </Link></li>
                        <li><Link target="_blank" to="/prolaps-form">    </Link></li>
                        <li><Link target="_blank" to="/diagnostics-form"> </Link></li>
                        <li><Link target="_blank" to="/pmdr-form">  ""</Link></li>
                        <li><Link target="_blank" to="/migrain-form">-   </Link></li>
                        <li><Link target="_blank" to="/krisp-form">   ()</Link></li>
                        <li><Link target="_blank" to="/pedt-form">     (PEDT)</Link></li>
                        <li><Link target="_blank" to="/ams-form">      (AMS)</Link></li>
                        <li><Link target="_blank" to="/mief-form">     (-5)</Link></li>
                    </ul>
                )}
            </div>

            <div className="menu_section">
                <h2 className='tables' onClick={toggleTables}></h2>
                {showTables && (
                    <ul className="dropdown">
                        <li><Link target="_blank" to="/contracepts-table"> </Link></li>
                        <li><Link target="_blank" to="/vteo-table">     /       </Link></li>
                        <li><Link target="_blank" to="/nmg-table">          </Link></li>
                        <li><Link target="_blank" to="/holestaz-table">    </Link></li>
                        <li><Link target="_blank" to="/coloscopy-table">   </Link></li>
                    </ul>
                )}
            </div>
        </div>
    );
}

export default Header;